import common from '../../common/common.js'
export default {
	data() {
		return {
			record: [],
			xia: 1,
			page: 1,
			pageSize: 10,
			warnType: 1
		}
	},
	onLoad() {
		this.getList();
	},
	methods: {
		// 获取报警记录
		getList() {
			var data = {
				page: this.page,
				pageSize: this.pageSize,
				isCheck: 1,
				warnType: this.warnType,
			}
			var that = this;
			common.getData('/muqiang/invitation/getWarnList', data, (res) => {
				this.record = res.pageInfo.list;
			})
		},
		// 下拉滑动
		lower() {
			var pageSize = this.pageSize;
			pageSize = pageSize + 10;
			this.pageSize = pageSize;
			this.getList();
		},
		// 底部下划线
		worn() {
			this.xia = 1;
			this.warnType = 1;
			this.getList();
		},
		full() {
			this.xia = 2;
			this.warnType = 2;
			this.getList();
		},
		// 跳转已完成详情
		goInfo(e) {
			var id = e.currentTarget.id;
			uni.navigateTo({
				url: '../service/service?id='+id
			})
		},
		// 跳转报警详情
		police(e) {
			var id = e.currentTarget.id;
			uni.navigateTo({
				url: '../police/police?id='+id
			})
		}
	}
}
